
import Vue, { defineComponent } from 'vue';

export default defineComponent({
    name: 'StdButton',
    props: {
        text: String,
        icon: String
    },
    methods: {
        // Emit btn-(this.text)-click
        onClick() {
            this.$emit('btn-' + this.text + '-click'); // ex: 'btn-open-click'
        }
    }
})
